:root{
	--size-base: 1rem;
	
	--size-big: clamp(3.125rem, 2.309vw + 2.527rem, 4.375rem);
	--size-large: clamp(1.25rem, 0vw + 1.25rem, 1.25rem); 
	--size-medium: clamp(1.625rem, 0.462vw + 1.505rem, 1.875rem);
	--size-small:  clamp(1rem, 0.115vw + 0.97rem, 1.063rem); 
	--size-tiny:  clamp(0.75rem, 0.462vw + 0.63rem, 1rem); 
} 